import { useState, useMemo, useCallback, useEffect } from 'react';
import { useUpdateEffect } from 'react-use';
import Sound from 'react-sound';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import ReplayIcon from '@material-ui/icons/Replay';
import PauseIcon from '@material-ui/icons/Pause';
import FastRewindIcon from '@material-ui/icons/FastRewind';
import FastForwardIcon from '@material-ui/icons/FastForward';
import CircularProgress from '@material-ui/core/CircularProgress';
import { moment } from '@src/lib';
import { EE } from '@src/utils';
import { useMediaState } from '@src/components';

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
  },
  buffer: {
    position: 'absolute',
    left: 0,
    top: 0,
    bottom: 0,
    transition: 'width 200ms ease-out',
    opacity: theme.palette.type === 'light' ? 0.1 : 0.15,
    background:
      theme.palette.type === 'light' ? theme.palette.secondary.main : theme.palette.primary.main,
  },
  progress: {
    position: 'absolute',
    left: 0,
    top: 0,
    bottom: 0,
    transition: 'width 200ms ease-out',
    opacity: theme.palette.type === 'light' ? 0.15 : 0.4,
    background:
      theme.palette.type === 'light' ? theme.palette.secondary.main : theme.palette.primary.main,
  },
  duration: {
    position: 'absolute',
    top: 0,
    bottom: 0,
    right: 8,
    lineHeight: '38px',
  },
  action: {
    paddingTop: 4,
    paddingBottom: 4,
  },
}));

/**
 * @param {object} props
 * @param {MessageEntity} props.message
 */
export function AudioCard({ message }) {
  const classes = useStyles();
  const [mediaState, dispathMediaAction] = useMediaState();

  const [loaded, setLoaded] = useState(false);
  const [playStatus, setPlayStatus] = useState(/** @type {SoundPlayStatus} */ ('STOPPED'));
  const [position, setPosition] = useState(0);
  const [duration, setDuration] = useState(0);
  const [bufferPercent, setBufferPercent] = useState(0);

  const progressPercent = useMemo(() => {
    if (!duration) {
      return 0;
    }
    return (100 * position) / duration;
  }, [position, duration]);

  const readableDuration = useMemo(() => {
    if (!duration) {
      return null;
    }

    const remain = duration - position;
    const md = moment.duration(remain);
    const parts = [md.minutes(), md.seconds()];
    if (md.hours() > 0) {
      parts.unshift(md.hours());
    }
    return parts.map(x => String(x).padStart(2, '0')).join(':');
  }, [position, duration]);

  const updatePlayStatus = useCallback(
    (/** @type {SoundPlayStatus} */ newPlayStatus) => {
      if (!loaded && newPlayStatus === 'PAUSED') {
        return;
      }

      setPlayStatus(newPlayStatus);

      if (newPlayStatus === 'PLAYING') {
        dispathMediaAction({
          type: 'switchPlayingMediaId',
          payload: { id: message.attachment?.id },
        });
      }
    },
    [message, loaded, dispathMediaAction],
  );

  // 快退
  const fastRewind = useCallback(() => {
    if (playStatus !== 'PLAYING') {
      updatePlayStatus('PLAYING');
    }

    // -10%
    const newProgressPercent = Math.max(0, progressPercent - 10);
    setPosition((duration * newProgressPercent) / 100);
  }, [playStatus, duration, progressPercent, updatePlayStatus]);

  // 快进
  const fastForward = useCallback(() => {
    if (playStatus !== 'PLAYING') {
      updatePlayStatus('PLAYING');
    }
    if (progressPercent >= 100) {
      setPosition(0);
    } else {
      // +10%
      const newProgressPercent = Math.min(progressPercent + 10, Math.min(bufferPercent, 100));
      setPosition((duration * newProgressPercent) / 100);
    }
  }, [playStatus, duration, progressPercent, bufferPercent, updatePlayStatus]);

  useUpdateEffect(() => {
    if (message.attachment.id === mediaState.playingMediaId) {
      if (playStatus !== 'PLAYING') {
        updatePlayStatus('PLAYING');
      }
    } else {
      if (playStatus === 'PLAYING') {
        updatePlayStatus('PAUSED');
      }
    }
  }, [message, mediaState.playingMediaId]);

  useEffect(() => {
    const playNextAudio = ({ id }) => {
      if (id === message.attachment?.id) {
        updatePlayStatus('PLAYING');
      }
    };

    EE.on('replyNextPlayingMediaId', playNextAudio);

    return () => {
      EE.off('replyNextPlayingMediaId', playNextAudio);
    };
  }, [message, updatePlayStatus]);

  return (
    <>
      <Card elevation={0} className={classes.root}>
        <div className={classes.buffer} style={{ width: `${bufferPercent}%` }} />
        <div className={classes.progress} style={{ width: `${progressPercent}%` }} />
        <Typography
          variant="inherit"
          display="block"
          noWrap
          color="textSecondary"
          className={classes.duration}
        >
          {readableDuration || '--:--'}
        </Typography>
        <CardActions className={classes.action}>
          <IconButton
            size="small"
            onClick={() => {
              updatePlayStatus(playStatus !== 'PLAYING' ? 'PLAYING' : 'PAUSED');
            }}
          >
            {playStatus === 'PLAYING' && (
              <>
                {progressPercent >= 100 && <ReplayIcon fontSize="default" color="action" />}
                {progressPercent < 100 && (
                  <>
                    {loaded ? (
                      <PauseIcon fontSize="default" color="action" />
                    ) : (
                      <CircularProgress size={24} color="secondary" disableShrink />
                    )}
                  </>
                )}
              </>
            )}
            {playStatus !== 'PLAYING' && (
              <>
                {progressPercent < 100 && <PlayArrowIcon fontSize="default" color="action" />}
                {progressPercent >= 100 && <ReplayIcon fontSize="default" color="action" />}
              </>
            )}
          </IconButton>
          <div />
          <IconButton size="small" onClick={fastRewind}>
            <FastRewindIcon fontSize="small" color="action" />
          </IconButton>
          <IconButton size="small" onClick={fastForward}>
            <FastForwardIcon fontSize="small" color="action" />
          </IconButton>
        </CardActions>
      </Card>
      <Sound
        url={message.attachment?.url}
        playStatus={playStatus}
        autoLoad={false}
        loop={false}
        playbackRate={mediaState.playbackRate}
        position={position}
        onLoading={({ bytesLoaded, bytesTotal, duration: dur }) => {
          if (!duration) {
            setDuration(dur);
          }

          setBufferPercent((100 * bytesLoaded) / bytesTotal);
        }}
        onLoad={({ loaded }) => {
          setLoaded(loaded);

          if (loaded && mediaState.playingMediaId !== message.attachment?.id) {
            updatePlayStatus('PLAYING');
          }
        }}
        onPlaying={({ position }) => {
          setPosition(position);
        }}
        onPause={({ position }) => {
          setPosition(position);
        }}
        onResume={({ position }) => {
          setPosition(position);
        }}
        onStop={({ position }) => {
          setPosition(position);
        }}
        onBufferChange={bool => {
          setLoaded(!bool);
        }}
        onFinishedPlaying={() => {
          updatePlayStatus('STOPPED');
          EE.emit('requestNextPlayingMediaId');
        }}
        onError={({ errorCode, description }) => {
          console.log('error', { errorCode, description });
        }}
      />
    </>
  );
}
