import { useCallback, useState, useEffect } from 'react';
import { orange, grey } from '@material-ui/core/colors';
import { EE } from '@src/utils';

export function useTheme() {
  const getNextTheme = useCallback((
    /** @type {import('@material-ui/core/styles').ThemeOptions} */ theme,
  ) => {
    const { type } = theme?.palette || {};
    const paletteMode = type === 'light' ? 'dark' : 'light';

    /** @type {import('@material-ui/core/styles').ThemeOptions} */
    const nextTheme = {
      palette: {
        type: paletteMode,
        primary: {
          main: paletteMode === 'light' ? orange['A700'] : grey['900'],
        },
        secondary: {
          main: paletteMode === 'light' ? orange['A400'] : grey['100'],
        },
        background: {
          default: paletteMode === 'light' ? grey['300'] : grey['700'],
        },
      },
    };

    return nextTheme;
  }, []);

  const [theme, setTheme] = useState(() => getNextTheme(null));

  const toggleTheme = useCallback(() => {
    setTheme(getNextTheme(theme));
  }, [theme, getNextTheme]);

  useEffect(() => {
    EE.on('toggle-theme', toggleTheme);
    return () => EE.off('toggle-theme', toggleTheme);
  }, [toggleTheme]);

  return { theme, toggleTheme };
}
